import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CartServiceService } from 'src/app/services/cart-service.service';

@Component({
  selector: 'app-car-list',
  templateUrl: './car-list.component.html',
  styleUrls: ['./car-list.component.scss']
})
export class CarListComponent implements OnInit {

  cartList = [];
  cartTotal = 0;
  shipping = 5;


  constructor(private cartService:CartServiceService, private rout: Router) { }

  ngOnInit() {
    this.loadCartItem();
    this.cartTotal =  CartServiceService.cartTotal;
    
  }

  loadCartItem(){
   this.cartList = this.cartService.GetCartItems();
  }

  deleteProductToCart(product){
    this.cartService.deleteProductToCart(product);
  }
  purchaseSuccess(){

    alert("Your Order has been Placed Successfully");

    this.rout.navigateByUrl('/home');

  }
}
